import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtro'
})

export class FiltroPipe implements PipeTransform {

  transform( arreglo: any[],
    texto: string,
    columna: string ): any[] {

    if ( texto === '' ) {
    return arreglo;
    }

    texto = texto.toLowerCase();

    if(arreglo == null){
      return null;
    }

    return arreglo.filter( item => {
    return item[columna].toLowerCase()
        .includes( texto );
    });

}

}
